.product-detail {
  flex: 1;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(2, 1fr);
  margin-top: 2rem;

  // Product info
  .product-detail-image {
    grid-column: 2/3;
    text-align: center;

    img {
      // width: auto;
      // height: auto;
    }
  }

  .product-detail-info {
    margin-top: 5rem;
    margin-left: 5rem;

    .product-detail-title {
      margin-bottom: 0.5rem;
      font-size: 2.5rem;
      font-weight: bold;

      > span {
        margin-right: 2rem;
      }
    }
    .product-detail-price {
      margin-bottom: 1rem;
      font-size: 2rem;

      > span {
        margin-right: 2rem;
      }
    }
    .product-detail-rate {
      margin-bottom: 1.5rem;
    }
    .product-detail-description {
      font-size: 2rem;
      > span {
      }

      > p {
        color: grey;
        font-size: 1.7rem;
        text-indent: 2rem;
      }
    }
    .product-detail-quantity {
      display: flex;

      .product-detail-quantity-display {
        margin-right: 1.5rem;
      }
    }
  }

  // Comment
  .product-detail-comment {
    grid-row: 2/3;
    grid-column: 2/4;
  }
}

// Carousel
.carousel {
  .control-arrow,
  &.carousel-slider .control-arrow {
    transition: all 0.25s ease-in;
    opacity: 0.4;
    position: absolute;
    z-index: 2;
    top: 20px;
    background: 0 0;
    border: 0;
    font-size: 32px;
    cursor: pointer;
    width: 40px;
  }

  .control-arrow {
    &:hover {
      opacity: 1;
    }

    &:before {
      margin: 0 5px;
      display: inline-block;
      border-top: 8px solid transparent;
      border-bottom: 8px solid transparent;
      content: '';
    }
  }

  &.carousel-slider .control-arrow:before {
    margin: 0 5px;
    display: inline-block;
    border-top: 8px solid transparent;
    border-bottom: 8px solid transparent;
    content: '';
  }

  .control-disabled.control-arrow {
    opacity: 0;
    cursor: inherit;
    display: none;
  }

  .control-prev.control-arrow {
    left: 0;

    &:before {
      border-right: 8px solid #fff;
    }
  }

  .control-next.control-arrow {
    right: 0;

    &:before {
      border-left: 8px solid #fff;
    }
  }

  position: relative;
  width: 100%;

  * {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
  }

  img {
    width: 100%;
    display: inline-block;
    pointer-events: none;
  }

  .carousel {
    position: relative;
  }

  .control-arrow {
    outline: 0;
    border: 0;
    background: 0 0;
    top: 50%;
    margin-top: -13px;
    font-size: 18px;
  }

  .thumbs-wrapper {
    margin: 20px;
    overflow: hidden;
  }

  .thumbs {
    -webkit-transition: all 0.15s ease-in;
    -moz-transition: all 0.15s ease-in;
    -ms-transition: all 0.15s ease-in;
    -o-transition: all 0.15s ease-in;
    transition: all 0.15s ease-in;
    -webkit-transform: translate3d(0, 0, 0);
    -moz-transform: translate3d(0, 0, 0);
    -ms-transform: translate3d(0, 0, 0);
    -o-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
    position: relative;
    list-style: none;
    white-space: nowrap;
  }

  .thumb {
    transition: border 0.15s ease-in;
    display: inline-block;
    margin-right: 6px;
    white-space: nowrap;
    overflow: hidden;
    border: 3px solid #fff;
    padding: 2px;

    &:focus {
      border: 3px solid #ccc;
      outline: 0;
    }

    &.selected,
    &:hover {
      border-color: #40a9ff;
      box-shadow: 0 0 0 2px rgba(24, 144, 255, 0.2);
    }

    img {
      vertical-align: top;
      height: 7rem;
      width: 7rem;
    }
  }

  &.carousel-slider {
    position: relative;
    margin: 0;
    overflow: hidden;

    .control-arrow {
      top: 0;
      color: #fff;
      font-size: 26px;
      bottom: 0;
      margin-top: 0;
      padding: 5px;

      &:hover {
        background: rgba(0, 0, 0, 0.2);
      }
    }
  }

  .slider-wrapper {
    overflow: hidden;
    margin: auto;
    width: 100%;
    -webkit-transition: height 0.15s ease-in;
    -moz-transition: height 0.15s ease-in;
    -ms-transition: height 0.15s ease-in;
    -o-transition: height 0.15s ease-in;
    transition: height 0.15s ease-in;

    &.axis-horizontal .slider {
      -ms-box-orient: horizontal;
      display: -webkit-box;
      display: -moz-box;
      display: -ms-flexbox;
      display: -moz-flex;
      display: -webkit-flex;
      display: flex;

      .slide {
        flex-direction: column;
        flex-flow: column;
      }
    }

    &.axis-vertical {
      -ms-box-orient: horizontal;
      display: -webkit-box;
      display: -moz-box;
      display: -ms-flexbox;
      display: -moz-flex;
      display: -webkit-flex;
      display: flex;

      .slider {
        -webkit-flex-direction: column;
        flex-direction: column;
      }
    }
  }

  .slider {
    margin: 0;
    padding: 0;
    position: relative;
    list-style: none;
    width: 100%;

    &.animated {
      -webkit-transition: all 0.35s ease-in-out;
      -moz-transition: all 0.35s ease-in-out;
      -ms-transition: all 0.35s ease-in-out;
      -o-transition: all 0.35s ease-in-out;
      transition: all 0.35s ease-in-out;
    }
  }

  .slide {
    min-width: 100%;
    margin: 0;
    position: relative;
    text-align: center;
    background: #fff;

    img {
      width: 100%;
      vertical-align: top;
      border: 0;
    }

    iframe {
      display: inline-block;
      width: calc(100% - 80px);
      margin: 0 40px 40px;
      border: 0;
    }

    .legend {
      -webkit-transition: all 0.5s ease-in-out;
      -moz-transition: all 0.5s ease-in-out;
      -ms-transition: all 0.5s ease-in-out;
      -o-transition: all 0.5s ease-in-out;
      transition: all 0.5s ease-in-out;
      position: absolute;
      bottom: 40px;
      left: 50%;
      margin-left: -45%;
      width: 90%;
      border-radius: 10px;
      background: #000;
      color: #fff;
      padding: 10px;
      font-size: 12px;
      text-align: center;
      opacity: 0.25;
      -webkit-transition: opacity 0.35s ease-in-out;
      -moz-transition: opacity 0.35s ease-in-out;
      -ms-transition: opacity 0.35s ease-in-out;
      -o-transition: opacity 0.35s ease-in-out;
      transition: opacity 0.35s ease-in-out;
    }
  }

  .control-dots {
    position: absolute;
    bottom: 0;
    margin: 10px 0;
    text-align: center;
    width: 100%;

    .dot {
      -webkit-transition: opacity 0.25s ease-in;
      -moz-transition: opacity 0.25s ease-in;
      -ms-transition: opacity 0.25s ease-in;
      -o-transition: opacity 0.25s ease-in;
      transition: opacity 0.25s ease-in;
      opacity: 0.3;
      box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.9);
      background: #fff;
      border-radius: 50%;
      width: 8px;
      height: 8px;
      cursor: pointer;
      display: inline-block;
      margin: 0 8px;

      &.selected,
      &:hover {
        opacity: 1;
      }
    }
  }

  .carousel-status {
    position: absolute;
    top: 0;
    right: 0;
    padding: 5px;
    font-size: 10px;
    text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.9);
    color: #fff;
  }

  &:hover .slide .legend {
    opacity: 1;
  }
}

@media (max-width: 48em) {
  .product-detail {
    grid-template-columns: repeat(1, 1fr);
    grid-template-rows: 1fr auto 1fr;

    // Product info
    .product-detail-image {
      grid-column: auto;
      grid-row: 1/2;
      text-align: center;

      img {
      }
    }

    .product-detail-info {
      grid-column: auto;
      grid-row: 2/3;
      margin: 0 1rem 2rem;

      .product-detail-title {
      }
      .product-detail-price {
      }
    }

    // Comment
    .product-detail-comment {
      grid-column: auto;
      grid-row: 3/4;
      padding: 0 2rem;
    }
  }

  .carousel-slider {
    width: 80% !important;
    margin: 0 auto !important;
  }

  .carousel {
    width: 80%;
    margin: 0 auto;

    .control-dots {
      bottom: 0;
    }
  }
}
